<template>
  <div>
    <productArea
      v-for="product in products"
      :key="product._id"
      :product="product"
      :addShopCar="addShopCar"
    />
  </div>
</template>

<script>
import productArea from "@/pages/productArea";
import axios from "axios";
export default {
  name: "category",
  components: { productArea },
  data() {
    return {
      category: "",
      products: [],
    };
  },
  mounted() {
    // localStorage.setItem("products", JSON.stringify(this.products));

    axios.get(`http://localhost:5005/categoryProduct2`).then((response) => {
      this.products = response.data;
      // console.log(this.products);
    });
  },
  methods: {
    addShopCar(product) {
      // console.log(this.userInfo);
      if (sessionStorage.getItem("userInfo")) {
        axios
          .post("http://localhost:5005/addShopCar", {
            userInfo: this.userInfo,
            product: {
              name: product.name,
              price: product.price,
              productImg: product.productImg,
              number: product.number,
              buyNumber: product.buyNumber,
              select: product.select,
              category: product.category,
            },
          })
          .then((res) => {
            alert(res.data);
          });
      } else {
        alert("请先登录");
      }
    },
  },
};
</script>

