<template>
  <div>
    <div style="margin: 20px"></div>
    <el-form
      :label-position="labelPosition"
      label-width="80px"
      :model="formLabelAlign"
    >
      <el-form-item label="商品名称">
        <el-input v-model="formLabelAlign.name"></el-input>
      </el-form-item>
      <el-form-item label="商品价格">
        <el-input v-model="formLabelAlign.price"></el-input>
      </el-form-item>
      <el-form-item label="商品种类">
        <el-input v-model="formLabelAlign.category"></el-input>
      </el-form-item>
      <el-form-item label="商品库存">
        <el-input v-model="formLabelAlign.number"></el-input>
      </el-form-item>
      <el-form-item label="商品图片">
        <el-input v-model="formLabelAlign.img"></el-input>
      </el-form-item>
      <el-button type="success" class="btn" @click="addProduct">添加</el-button>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "AddProduct",
  data() {
    return {
      labelPosition: "right",
      formLabelAlign: {
        name: "",
        price: "",
        category: "",
        number: "",
        img: "   ",
      },
    };
  },
  methods: {
    addProduct() {
      axios
        .post("http://localhost:5005/addProduct", {
          product: {
            name: this.formLabelAlign.name,
            price: this.formLabelAlign.price,
            productImg: this.formLabelAlign.img,
            number: this.formLabelAlign.number,
            buyNumber: 1,
            select: false,
            category: this.formLabelAlign.category,
          },
        })
        .then((res) => {
          this.allProduct = res.data;
          console.log(res.data);
        });
      alert("加入成功");
      this.formLabelAlign.name =
        this.formLabelAlign.price =
        this.formLabelAlign.img =
        this.formLabelAlign.number =
        this.formLabelAlign.category =
          "";
    },
  },
};
</script>

<style scoped>
.btn {
  margin-left: 450px;
}
</style>
