<template>
  <div>
    <div v-for="order in orders" :key="order._id" class="orderArea">
      <div class="info">用户名：{{ order.userInfo }}</div>
      <div class="info">收货人：{{ order.name }}</div>
      <div class="info">收货地址：{{ order.region }}</div>
      <div class="info">联系方式：{{ order.tel }}</div>
    </div>
    <OrderProduct
      style="clear: both"
      v-for="product in allProduct"
      :key="product._id"
      :product="product"
    />
  </div>
</template>

<script>
import OrderProduct from "@/pages/OrderProduct";

import axios from "axios";
export default {
  name: "Order",
  data() {
    return {
      orders: [],
      allProduct: [],
    };
  },
  components: { OrderProduct },

  mounted() {
    axios.get("http://localhost:5005/allorders").then((response) => {
      this.orders = response.data;

      // console.log(this.orders);
      // console.log(this.orders[0].userInfo);
      axios
        .get(
          `http://localhost:5005/orderProduct?userInfo=${this.orders[0].userInfo}`
        )
        .then((response) => {
          this.allProduct = response.data;
        });
    });
  },
};
</script>

<style scoped>
.orderArea {
  width: 1000px;
  /* height: 500px; */
  background-color: #bfa;
}
.info {
  width: 500px;
  height: 50px;
  line-height: 50px;
  font-size: 25px;
  background-color: rgb(129, 129, 129);
  color: aliceblue;
  float: left;
  text-align: center;
}
</style>
