<template>
  <div>
    <productArea
      v-for="product in products"
      :key="product._id"
      :product="product"
      :addShopCar="addShopCar"
    />
  </div>
</template>

<script>
import productArea from "@/pages/productArea";
import axios from "axios";

export default {
  name: "searchProduct",
  components: {
    productArea,
  },
  data() {
    return {
      keyword: "",
      products: [],
    };
  },
  methods: {
    addShopCar(product) {
      if (sessionStorage.getItem("userInfo")) {
        axios
          .post("http://localhost:5005/addShopCar", {
            userInfo: this.userInfo,
            product: {
              name: product.name,
              price: product.price,
              productImg: product.productImg,
              number: product.number,
              buyNumber: product.buyNumber,
              select: product.select,
              category: product.category,
            },
          })
          .then((res) => {
            alert(res.data);
          });
      } else {
        alert("请先登录");
      }
    },
    writeProducts(key) {
      // console.log(111);
      this.keyword = key;
      // this.products = [];
      // let reg = RegExp(this.keyword);
      // let ps = JSON.parse(localStorage.getItem("products"));
      // ps.forEach((p) => {
      //   if (reg.test(p.name)) {
      //     this.products.unshift(p);
      //   }
      // });
      if (this.keyword == "") {
        this.products = [];
      } else {
        axios
          .get(`http://localhost:5005/SearchProduct?name=${this.keyword}`)
          .then((response) => {
            this.products = response.data;
          });
      }
    },
  },
  mounted() {
    this.$bus.$on("getProducts", this.writeProducts);
  },
};
</script>

