<template>
  <div id="root">
    <div class="login-wrap">
      <h2>注册</h2>
      <div class="form">
        <input
          type="text"
          placeholder="用户名"
          name="un"
          v-model="userInfo.name"
        />
        <input
          type="password"
          placeholder="密码"
          name="pw1"
          v-model="userInfo.password"
        />
        <input
          type="password"
          placeholder="确认密码"
          name="pw2"
          v-model="password2"
        />
        <button @click="sign">注册</button>
        <router-link :to="url"><p>我已注册，去登录</p></router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "register",
  data() {
    return {
      url: "/Login",
      userInfo: {
        name: "",
        password: "",
      },
      password2: "",
    };
  },
  methods: {
    sign() {
      console.log(this);
      if (
        this.userInfo.password == this.password2 &&
        this.userInfo.name !== ""
      ) {
        localStorage.setItem(this.userInfo.name, JSON.stringify(this.userInfo));
        localStorage.setItem(this.userInfo.name + "register", true);
        alert("请返回登录");
      } else {
        alert("两次密码输入的不一样");
      }
    },
  },
};
</script>

<style scoped>
#root {
  width: 1200px;
  height: 600px;
  background: url(image/img1.jpg) no-repeat;
  /* background-color: #bfa; */
  position: relative;
  margin: 0 auto;
}
.login-wrap {
  position: absolute;
  background: #ecf0f1;
  width: 350px;
  border-radius: 5px;
  box-shadow: 3px 3px 10px #333;
  padding: 15px;
  top: 100px;
  left: 700px;
}
.login-wrap h2 {
  text-align: center;
  font-weight: 200;
  font-size: 1.6em;
  margin-top: 20px;
  color: #34495e;
  height: 50px;
}
.login-wrap .form {
  padding-top: 20px;
}
.login-wrap .form input[type="text"],
.login-wrap .form input[type="password"],
.login-wrap .form button {
  width: 80%;
  margin-left: 10%;
  margin-bottom: 25px;
  height: 40px;
  border-radius: 5px;
  outline: 0;
}
.login-wrap .form input[type="text"],
.login-wrap .form input[type="password"] {
  border: 1px solid #bbb;
  padding: 0 0 0 10px;
  font-size: 14px;
}
.login-wrap .form input[type="text"]:focus,
.login-wrap .form input[type="password"]:focus {
  border: 1px solid #3498db;
}
.login-wrap .form a {
  text-align: center;
  font-size: 12px;
  color: #3498db;
}
.login-wrap .form a p {
  padding-bottom: 10px;
}
.login-wrap .form button {
  background: #e74c3c;
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 200;
  cursor: pointer;
  transition: box-shadow 0.4s ease;
}
.login-wrap .form button:hover {
  box-shadow: 1px 1px 5px #555;
}
.login-wrap .form button:active {
  box-shadow: 1px 1px 7px #222;
}
.login-wrap:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  border-radius: 5px 5px 0 0;
}
p {
  position: absolute;
  left: 140px;
  bottom: 5px;
}
</style>
