<template>
  <div id="productArea">
    <div id="productInfoArea">
      <div id="productInfo">
        <img class="productImg" :src="product.productImg" alt="#" />
        <span>{{ product.name }}</span>
        <span>￥{{ product.price }}</span>
        <img
          @click="sendProductName"
          src="@/assets/car.png"
          alt=""
          class="car"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "productArea",
  props: ["product", "addShopCar"],
  methods: {
    sendProductName() {
      this.addShopCar(this.product);
    },
  },
};
</script>

<style scoped>
#productArea {
  width: 1200px;
  /* height: 1200px; */
  background-color: #68945c;
  margin: 0 auto;
  text-align: center;
}
#productArea:hover {
  cursor: pointer;
}
.productInfoArea {
  width: 1200px;
}
#productInfo {
  width: 300px;
  height: 400px;
  background-color: #fff;
  float: left;
  position: relative;
}
.productImg {
  width: 300px;
  height: 300px;
}
#productInfo span {
  font-size: 20px;
  display: block;
}
.productImg:hover {
  transform: scale(1.2);
}
.car {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 30px;
  bottom: 50px;
}
</style>
