//配置路由的地方
import Vue from "vue";
import VueRouter from "vue-router";

//使用插件
Vue.use(VueRouter);
// 引入路由组件;
import MarketHome from "@/pages/MarketHome";
import Admin from "@/pages/Admin";
import AddProduct from "@/pages/AddProduct";
import DelProduct from "@/pages/DelProduct";
import Order from "@/pages/Order";
import UserInfo from "@/pages/UserInfo";

import category1 from "@/pages/category1";
import category2 from "@/pages/category2";
import category3 from "@/pages/category3";
import category4 from "@/pages/category4";
import MarketSearch from "@/pages/MarketSearch";
import MarketLogin from "@/pages/MarketLogin";
import register from "@/pages/register";
import myShopCar from "@/pages/myShopCar";
import SubmitOrder from "@/pages/SubmitOrder";

//配置路由
export default new VueRouter({
  //配置路由
  routes: [
    {
      path: "/Home",
      component: MarketHome,
    },

    {
      path: "/Search",
      component: MarketSearch,
    },
    {
      path: "/Login",
      component: MarketLogin,
      //一级路由加/ ，子路由不用
      // children: [
      //   {
      //     path: "register",
      //     component: register,
      //     // beforeEnter(to, from, next) {
      //     //   console.log(to, from);
      //     // },
      //   },
      // ],
    },
    {
      path: "/Register",
      component: register,
    },
    {
      path: "/myShopCar",
      component: myShopCar,
    },
    {
      path: "/submitOrder",
      component: SubmitOrder,
    },
    {
      path: "/Admin",
      component: Admin,
      children: [
        {
          path: "addProduct",
          component: AddProduct,
        },
        {
          path: "delProduct",
          component: DelProduct,
        },
        {
          path: "order",
          component: Order,
        },
        {
          path: "userInfo",
          component: UserInfo,
        },
      ],
    },
    {
      path: "/category1",
      component: category1,
    },
    {
      path: "/category2",
      component: category2,
    },
    {
      path: "/category3",
      component: category3,
    },
    {
      path: "/category4",
      component: category4,
    },
    //重定向，在项目跑起来的时候，访问/,立马让他定向到首页
    {
      path: "*",
      redirect: "/Home",
    },
  ],
});
