<template>
  <div id="root">
    <input
      type="checkbox"
      v-model="product.select"
      @click="selectItem(product._id)"
    />
    <div class="imgArea"><img :src="product.productImg" alt="" /></div>
    <div class="productName">{{ product.name }}</div>
    <div class="productPrice">￥{{ product.price }}</div>
    <div class="numberArea">
      <button @click="reduceNumber(product.id)">-</button>
      {{ product.buyNumber }}
      <button @click="riseNumber(product.id)">+</button>
    </div>
    <div class="allPrice">合计：{{ product.price * product.buyNumber }}</div>
    <div class="delete">
      <button id="delete" @click="deleteItem(product._id)">删除</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "productItem",
  props: ["product", "selectOne", "deleteOne"],
  data() {
    return {
      userInfo: "",
    };
  },
  methods: {
    //减少购买数量
    reduceNumber(id) {
      if (this.product.buyNumber !== 1) {
        this.product.buyNumber--;
        // axios.get("/UpdateShopCar/${}")
        axios
          .get(
            `http://localhost:5005/UpdateShopCar?id=${this.product._id}&buyNumber=${this.product.buyNumber}&userInfo=${this.userInfo}`
          )
          .then((response) => {});
      }
    },
    //增加购买数量
    riseNumber(id) {
      this.product.buyNumber++;
      axios
        .get(
          `http://localhost:5005/UpdateShopCar?id=${this.product._id}&buyNumber=${this.product.buyNumber}&userInfo=${this.userInfo}`
        )
        .then((response) => {
          // this.products = response.data;
        });
    },
    //选中功能
    selectItem(id) {
      if (this.product.select == true) {
        this.selectOne(id, -1);
      } else {
        this.selectOne(id, 1);
      }
    },
    //删除功能
    deleteItem(id) {
      this.deleteOne(id);
    },
  },
  mounted() {
    this.userInfo = sessionStorage.getItem("userInfo");
  },
};
</script>

<style scoped>
#root {
  cursor: pointer;
  width: 1100px;
  height: 160px;
  margin: 20px auto;
  background-color: #f5f5f5;
  position: relative;
  border-radius: 20px;
  font-size: 25px;
}
.imgArea {
  position: absolute;
  width: 120px;
  height: 120px;
  top: 20px;
  left: 50px;
}
img {
  width: 100%;
}
input {
  position: absolute;
  top: 20px;
  left: 20px;
}
.productName {
  /* background-color: #bfa; */
  height: 50px;
  width: 200px;
  position: absolute;
  left: 300px;
  top: 30px;
}
.productPrice {
  /* background-color: #bfa; */
  height: 50px;
  width: 100px;
  position: absolute;
  left: 600px;
  top: 30px;
}
.numberArea {
  position: absolute;
  left: 700px;
  top: 30px;
}
.allPrice {
  background-color: #fff;
  color: red;
  position: absolute;
  left: 850px;
  top: 30px;
}
.delete {
  position: absolute;
  left: 1000px;
  top: 30px;
}
.delete button:hover {
  color: red;
}
button {
  cursor: pointer;
  border: 0;
  font-size: 25px;
}
</style>
