<template>
  <div class="root">
    <div class="header">
      <h1>XX超市</h1>
      <router-link :to="url">
        <a class="carArea" @click="toAdmin" v-show="display"> 商品管理 </a>
      </router-link>
      <router-link :to="url">
        <a class="carArea" @click="toCar" v-show="!display">
          我的购物车
          <img src="@/assets/car.png" alt="" class="car" /> </a
      ></router-link>
      <input type="text" placeholder="商品名称" v-model="keyword" />
      <router-link to="/Search">
        <button class="search" type="submit" @click="sendProducts">搜索</button>
      </router-link>
      <hr color="black" />
      <div class="HeaderMenu">
        <ul class="HeaderMenu">
          <li><router-link to="/Home">网站首页</router-link></li>
          <li><router-link to="/category1">食品饮料</router-link></li>
          <li><router-link to="/category2">生活用品</router-link></li>
          <li><router-link to="/category3">美容洗护</router-link></li>
          <li><router-link to="/category4">家居家电</router-link></li>
          <li><router-link to="/Login">注册登录</router-link></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MarketHeader",

  data() {
    return {
      userInfo: "",
      display: false,
      keyword: "",
      url: "",
    };
  },
  methods: {
    writeUserInfo(username) {
      this.userInfo = username;
    },
    toCar() {
      if (sessionStorage.getItem("userInfo")) {
        this.url = "/MyShopCar";
      } else {
        alert("请先登录");
      }
    },
    toAdmin() {
      if (this.display) {
        this.url = "/Admin";
      }
    },
    sendProducts() {
      this.$bus.$emit("getProducts", this.keyword);
    },
  },
  mounted() {
    this.$bus.$on("getUserInfo", this.writeUserInfo);
    if (sessionStorage.getItem("userInfo") == "admin") {
      this.display = true;
    }
  },
};
</script>

<style scoped>
.root {
  width: 1200px;
  height: 116px;
  margin: 0 auto;
  position: relative;
  /* background-color: #779649; */
}
.header h1 {
  color: #06c961;
  font-size: 50px;
}
.header input {
  position: absolute;
  right: 50px;
  top: 20px;
  width: 200px;
  height: 30px;
}
.search {
  position: absolute;
  top: 20px;
  right: 0px;
  width: 50px;
  height: 33px;
  line-height: 33px;
  font-size: 15px;
  border: 1px solid gray;
}
.HeaderMenu {
  list-style-type: none;
}
.HeaderMenu li {
  float: left;
  width: 200px;
  height: 50px;
  text-align: center;
  background-color: #06c961;
  line-height: 50px;
}
.HeaderMenu li a {
  line-height: 30px;
  font-size: 15px;
  text-decoration: none;
  color: aliceblue;
}
.HeaderMenu a:hover {
  color: #ad5858;
}
.carArea {
  position: absolute;
  right: 290px;
  top: 30px;
}
.carArea:hover {
  color: #06c961;
  cursor: pointer;
}
.car {
  position: absolute;

  width: 20px;
  height: 20px;
}
</style>
