<template>
  <div>
    <productItemAdmin
      v-for="product in allProduct"
      :key="product._id"
      :product="product"
      :deleteOne="deleteOne"
    />
  </div>
</template>

<script>
import productItemAdmin from "@/pages/productItemAdmin";
import axios from "axios";

export default {
  name: "DelProduct",
  components: { productItemAdmin },
  data() {
    return {
      allProduct: [],
    };
  },
  methods: {
    //删除一个商品
    deleteOne(id) {
      if (confirm("确定删除吗？")) {
        axios
          .get(`http://localhost:5005/deleteOneProduct?id=${id}`)
          .then((response) => {
            this.allProduct = response.data;
          });
      }
    },
  },
  mounted() {
    axios.get("http://localhost:5005/allProducts").then((response) => {
      this.allProduct = response.data;
      // console.log(this.products);
    });
  },
};
</script>

