<template>
  <div id="submit">
    <div class="formArea">
      <el-form class="form1" ref="form" :model="form" label-width="80px">
        <el-form-item label="用户名">
          <el-input v-model="form.userInfo"></el-input>
        </el-form-item>
        <el-form-item label="收货人">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="收货地址">
          <el-input v-model="form.region"></el-input>
        </el-form-item>
        <el-form-item label="联系方式">
          <el-input v-model="form.tel"></el-input>
        </el-form-item>

        <el-form-item label="送货时间">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="form.date1"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-time-picker
              placeholder="选择时间"
              v-model="form.date2"
              style="width: 100%"
            ></el-time-picker>
          </el-col>
        </el-form-item>

        <el-button class="btn" type="primary" @click="onSubmit"
          >确认购买</el-button
        >
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "submitOrder",
  data() {
    return {
      form: {
        userInfo: "",
        name: "",
        region: "",
        tel: "",
        date1: "",
        date2: "",
      },
      buyProducts: [],
    };
  },
  mounted() {
    this.form.userInfo = sessionStorage.getItem("userInfo");
  },
  methods: {
    onSubmit() {
      // console.log(this.form);
      axios
        .post("http://localhost:5005/addOrder", {
          order: this.form,
        })
        .then((res) => {});
      alert("购买成功");
    },
  },
};
</script>

<style scoped>
#submit {
  width: 1200px;
  height: 800px;
  /* background-color: #bfa; */
  margin: 0 auto;
}
.formArea {
  width: 800px;
  margin: 0 auto;
}
.form1 {
  margin-top: 50px;
}
.btn {
  margin-left: 380px;
  margin-top: 100px;
}
</style>
