<template>
  <div>
    <div class="flexslider">
      <ul class="slides">
        <li class="li2"></li>
        <li class="li1"></li>
        <li class="li3"></li>
        <li class="li4"></li>
        <li class="li5"></li>
      </ul>
    </div>
    <span class="productSpan">产品展示</span>
    <productArea
      v-for="product in products"
      :key="product._id"
      :product="product"
      :addShopCar="addShopCar"
    />
  </div>
</template>

<script>
import productArea from "@/pages/productArea";
import axios from "axios";
export default {
  name: "MarketHome",
  components: { productArea },
  data() {
    return {
      userInfo: "",
      products: [],
    };
  },
  mounted() {
    this.userInfo = sessionStorage.getItem("userInfo");
    // localStorage.setItem("products", JSON.stringify(this.products));
    axios.get("http://localhost:5005/allProducts").then((response) => {
      this.products = response.data;
      // console.log(this.products);
    });
  },
  methods: {
    addShopCar(product) {
      // console.log(this.userInfo);
      if (sessionStorage.getItem("userInfo")) {
        axios
          .post("http://localhost:5005/addShopCar", {
            userInfo: this.userInfo,
            product: {
              name: product.name,
              price: product.price,
              productImg: product.productImg,
              number: product.number,
              buyNumber: product.buyNumber,
              select: product.select,
              category: product.category,
            },
          })
          .then((res) => {
            alert(res.data);
          });
      } else {
        alert("请先登录");
      }
    },
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a,
img {
  border: 0;
}
.flexslider {
  position: relative;
  height: 400px;
  overflow: hidden;
  background: url(images/loading.gif) 50% no-repeat;
  width: 1200px;
  margin: 0 auto;
}
.li1 {
  background: url(images/img1.jpg);
}
.li2 {
  background: url(images/img2.jpg);
}
.li3 {
  background: url(images/img3.jpg);
}
.li4 {
  background: url(images/img4.jpg);
}
.li5 {
  background: url(images/img5.jpg);
}
.slides {
  position: relative;
  z-index: 1;
}
.slides li {
  height: 400px;
}
.flex-control-nav {
  position: absolute;
  bottom: 10px;
  z-index: 2;
  width: 100%;
  text-align: center;
}
.flex-control-nav li {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin: 0 5px;
  *display: inline;
  zoom: 1;
}
.flex-control-nav a {
  display: inline-block;
  width: 14px;
  height: 14px;
  line-height: 40px;
  overflow: hidden;
  background: url(images/dot.png) right 0 no-repeat;
  cursor: pointer;
}
.flex-control-nav .flex-active {
  background-position: 0 0;
}

.flex-direction-nav {
  position: absolute;
  z-index: 3;
  width: 100%;
  top: 45%;
}
.flex-direction-nav li a {
  display: block;
  width: 50px;
  height: 50px;
  overflow: hidden;
  cursor: pointer;
  position: absolute;
}
.flex-direction-nav li a.flex-prev {
  left: 40px;
  background: url(images/prev.png) center center no-repeat;
}
.flex-direction-nav li a.flex-next {
  right: 40px;
  background: url(images/next.png) center center no-repeat;
}
/* 产品展示居中*/

.productSpan {
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
