<template>
  <div>
    <productItem
      v-for="product in myShopCar"
      :key="product._id"
      :product="product"
      :selectOne="selectOne"
      :deleteOne="deleteOne"
    />
    <div class="All">
      <div class="selectAll">
        <input type="checkbox" v-model="isAll" /> 全选
      </div>
      <div class="amount">已选择商品：{{ selectNumber }} 件</div>
      <div class="allPrice">合计：{{ allPrice }}</div>
      <router-link :to="order"
        ><div class="buy" @click="buy">购买</div></router-link
      >
    </div>
  </div>
</template>

<script>
import productItem from "@/pages/productItem";
import axios from "axios";
export default {
  name: "myShopCar",
  data() {
    return {
      username: "",
      myShopCar: [],
      selectNumber: 0,
      order: "",
      buyProducts: [],
    };
  },
  computed: {
    //判断是否全选
    isAll: {
      get() {
        if (this.myShopCar.length == 0) {
          return false;
        } else {
          return this.selectNumber === this.myShopCar.length;
        }
      },
      set(value) {
        this.selectAll(value);
      },
    },
    //计算总价
    allPrice() {
      let money = 0;
      this.myShopCar.forEach((product) => {
        if (product.select == true) {
          money = product.buyNumber * product.price + money;
        }
      });
      return money;
    },
  },

  methods: {
    // 选中一个商品
    selectOne(id, num) {
      // console.log(id);
      this.myShopCar.forEach((product) => {
        if (product._id == id) {
          product.select = !product.select;
          console.log(product.select);
          axios
            .get(
              `http://localhost:5005/UpdateShopCar?id=${product._id}&select=${product.select}&userInfo=${this.username}`
            )
            .then((response) => {
              // this.products = response.data;
            });
        }
      });
      this.selectNumber += num;
    },
    //全选
    selectAll(value) {},
    //删除一个商品
    deleteOne(id) {
      axios
        .get(
          `http://localhost:5005/deleteProduct?id=${id}&userInfo=${this.username}`
        )
        .then((response) => {
          this.myShopCar = response.data;
        });
    },
    //购买
    buy() {
      // alert("购买成功");
      this.buyProducts = this.myShopCar.filter((p) => {
        return p.select == true;
      });
      console.log(this.buyProducts[0]);
      if (this.buyProducts[0]) {
        // console.log(buyProducts);
        this.order = `/submitOrder?buyProducts=${this.buyProducts}`;
      }
    },
  },
  components: {
    productItem,
  },
  mounted() {
    this.username = sessionStorage.getItem("userInfo");
    // console.log(this.username);
    axios
      .get(`http://localhost:5005/myShopCar?userInfo=${this.username}`)
      .then((response) => {
        this.myShopCar = response.data;
      });
  },
};
</script>

<style scoped>
.All {
  cursor: pointer;
  border-top: 2px solid black;
  position: relative;
  width: 1200px;
  height: 80px;
  margin: 0 auto;
  font-size: 20px;
  /* background-color: #bfa; */
}
.selectAll {
  /* width: 50px; */
  height: 80px;
  line-height: 80px;
  position: absolute;
  left: 50px;
}
.allPrice {
  position: absolute;
  left: 800px;
  line-height: 80px;
}
.amount {
  line-height: 80px;
  position: absolute;
  left: 400px;
}
.buy {
  width: 60px;
  background-color: #ff5000;
  height: 30px;
  border-radius: 10px;
  color: aliceblue;
  text-align: center;
  position: absolute;
  left: 1050px;
  top: 25px;
}
.buy:hover {
  background-color: #ff3300;
}
a {
  text-decoration: none;
  color: aliceblue;
}
</style>
